<section class="privacy-policy">
	<div class="background">
		<h3>Privacy Policy</h3>
	</div>
	<div class="format">
		<div class="policy">
			<p>At <span>Word Clouds</span>, we harvest no user data of any kind. Period. When you opt into logging in using your <span>Facebook</span> account, your email address will be stored on our servers to ensure that your identity is maintained across each device you wish to play <span>Word Clouds</span> on.</p>
			<p>A link to <span>Facebook's</span> full privacy policy can be found <a target="blank" href="https://www.facebook.com/privacy/policy">here</a>.</p>
			<p>In addition to logging in with <span>Facebook</span>, we support serving advertisements to our players to offer:</p>
			<ol>
				<li>The ability to exchange watching a video for in-game currency. This currency can be used to purchase puzzle hints to get you through some of the more difficult puzzles.</li>
				<li>Personalized advertisement (hopefully) relevant to you.</li>
				<li>Us the ability to continue allowing our game to be forever available for free!</li>
			</ol>
			<p>Our advertizing provider is a company called <span>AppLovin</span>. They're one of the premier advertisers in the mobile gaming space and offer us a generous amount of control over the kind of ads our users see.</p>
			<p>A link to <span>AppLovin's</span> privacy policy can be found <a target="blank" href="https://www.applovin.com/privacy">here</a>.</p>
		</div>
		<div class="closing-remarks">
			<p>If at any point you'd like your email address removed from our servers, simply enter the <span>Word Clouds</span> app, and open the settings screen.</p>
			<p>At the bottom, you'll find a button that'll allow you to delete your account.</p>
			<p>By pressing this button, your email address will be removed from our servers without losing any of the progress you've made in <span>Word Clouds</span>.</p>
			<p>The only downside is that the ability to synchronize your progress across all of your devices will also be lost because we'll no longer have a way to identify you using your email address.</p>
			<p>We have a great amount of appreciation for all of our players, and hope you've enjoyed our game as much as we have!</p>
		</div>
	</div>
</section>

